




























































































































































    declare global {
        interface Window{ PrismicJS: any, PrismicDOM: any }
    }	

	import { Component, Vue } from "vue-property-decorator";
	import { Route } from 'vue-router';
	import _Prismic from '@/prismic';
	
    Component.registerHooks(['beforeRouteEnter']);

	@Component
	export default class Locations extends Vue {
		prismicDOM = window.PrismicDOM;
        Prismic = window.PrismicJS;
		zones: any = [];
        headquarter: any = {contact:[]};
        viewZone: boolean = false;
	    currentZone = '';
        message: any = {name:'', contact:'', contents:''};
		
		beforeRouteEnter(to: Route, from: Route, next: Function) {
			next((vm) => { vm.getPage(); });
		}

        async getPage() {
			let api = await _Prismic();
		    let results = (await api.query(window.PrismicJS.Predicates.at('document.type', 'about'),
				{ fetch:[ 'about.zones','about.headquarter_image', 'about.headquarter_contact' ]}
			)).results[0].data;
 
            this.zones = results.zones;
            this.headquarter = {
                image: results.headquarter_image.url, contact: results.headquarter_contact
            };
		}

        sendMail() {
            if((this.message.name == '') || (this.message.contact == '')) return;
			let connect = {
				category: 'location', ...this.message, zone: this.currentZone
			}
			fetch('http://' + process.env.VUE_APP_ENDPOINT + '/contact-us', {
				method: 'post', body: JSON.stringify(connect),
				headers:{ 'Content-Type': 'application/json; charset=utf-8'}
			});
            this.message = {};
            this.viewZone = false;
        }

		scrollTo(id: string) {
			var el = document.getElementById(id);
			el.scrollIntoView({behavior: 'smooth'});
		}
}
